<template>
    <Header class="dark"></Header>
    <SearchBrand></SearchBrand>
    <section class="brands">
        <div class="container">
            <div class="box-wrap">
                <h3 class="wrap-title text-center mb-4">Популярные марки</h3>
                <div class="row pt-3">
                    <template v-for="mark in marks" :key="mark">
                        <div class="col-md-3" v-if="mark.title && mark.count">
                            <router-link :to="'/auction?brand=' + mark.id" class="mark-list-item"><b>{{ String(mark.title).toLowerCase() }}</b><span>({{ mark.count }})</span></router-link>
                        </div>
                    </template>
                </div>
            </div>
        </div>
    </section>
    <Contacts></Contacts>
    <Footer></Footer>
</template>

<script>
import Header from '@/components/Header.vue'
import SearchBrand from '../components/SearchBrand.vue'
import Contacts from '@/components/Contacts.vue'
import Footer from '@/components/Footer.vue'

export default {
    components: {
        Header,
        SearchBrand,
        Contacts,
        Footer
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        marks() {
            return (this.$store.getters.getBrandImage)
            ? this.$store.getters.getBrandImage
            : this.$store.dispatch('getBrandImage')
        }
    },
}
</script>