export default {
    state: {
        baseUrl: 'https://api.autocanada.kg/'
    },
    mutations: {
        setRequestResponse(state, data) {
            state.requestData = data
        }
    },
    actions: {
        sendRequest({commit, state}, data) {
            return new Promise((resolve, reject) => {
                commit('clearNotification')
                if(!data.loader && data.loader !== false) {
                    commit('setLoading', true)
                }

                const finalUrl = (data.baseUrl) ? data.baseUrl : state.baseUrl + data.endpoint
                fetch(finalUrl, {
                    method: data.method ? data.method : 'POST',
                    timeout: 8000,
                    headers: {
                        'Content-Type':'application/json',
                        'Authorization': data?.token ? ` JWT ${data.token}` : null
                    },
                    body: JSON.stringify(data.data)
                })
                .then(response => {
                    if(!data.loader && data.loader !== false) {
                        commit('setLoading', false)
                    }
                    resolve(response)
                })
                .catch((err) => {
                    reject(err)
                })
            })
        }
    },
    getters: {
        getBaseUrl(state) {
            return state.baseUrl
        },
        getRequestData(state) {
            return state.requestData
        }
    }
}